document.addEventListener('DOMContentLoaded', function(){
    scrollNav();

    navegacionFija();
});

function navegacionFija(){

    const barra = document.querySelector('.header');
    //registrar el  interseccion observer

    const observer = new IntersectionObserver(function(entries){
       // console.log(entries[0]);
       if(entries[0].isIntersecting){
           // console.log('elemento visible');
           barra.classList.remove('fijo'); 
       }else{
           // console.log('desaparecio');
           barra.classList.add('fijo');
       }
    });

    
    //elemento observar
    observer.observe(document.querySelector('.video'));
}

function scrollNav(){
    const enlaces = document.querySelectorAll('.navegacion-principal a');
    enlaces.forEach(function(enlace){
        //console.log(enlace);
        enlace.addEventListener('click', function(elemento){
            elemento.preventDefault();
            console.log(elemento.target.attributes.href.value); 
            
            const seccion = document.querySelector(elemento.target.attributes.href.value);
            seccion.scrollIntoView({
                behavior: 'smooth'
            });
        });

    }); 
}
